import request from '@/utils/request'

let apidomain = 'http://' + window.location.hostname

export function getWxConfig(params){
  return request({
    url: apidomain + '/weixin/sign?url=' + window.location.href.split("#")[0],
    method: 'get',
    params
  })
}

export function getShareConfig(params){
  return request({
    url: apidomain + '/weixin/shard',
    method: 'get',
    params
  })
}

export function getAd(params){
  return request({
    url: apidomain + '/weixin/ad',
    method: 'get',
    params
  })
}



