import Vue from 'vue'
import Router from 'vue-router'
import share from '@/views/share/share'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  // base:'share',
  routes: [
    {
      path: '/',
      name: 'share',
      component: share
    }  
  ]
})



