<template>
  <mescroll-vue ref="mescroll" :down="mescrollDown">

    <div class="share_details">
      <div class="hd">
        <div class="ly_lt"></div>
        <div class="ly_rt"></div>
      </div>
      <div class="bd">
        <div v-if="isCountTixian" :class="'share share_' + count"></div>
        <div v-else class="share share_6" @click="goAd()"></div>
      </div>
      <div class="ft">
        <div>{{tip}}</div>
        <p>帮助a |	<a href="#">投诉</a></p>
      </div>
    </div>

  </mescroll-vue>
</template>

<script>
import MescrollVue from 'mescroll.js/mescroll.vue'
import { getWxConfig, getShareConfig, getAd } from '@/api/main'

export default {
  name: 'share',
  components: {
    MescrollVue
  },
  data () {
    return {
      tip: decodeURIComponent("%E5%A6%82%E6%9C%89%E4%BB%BB%E4%BD%95%E7%96%91%E9%97%AE%EF%BC%8C%E8%AF%B7%E8%81%94%E7%B3%BB%E5%AE%98%E6%96%B9%E5%AE%A2%E6%9C%8D%E5%B8%AE%E6%82%A8%E5%A4%84%E7%90%86%E3%80%82"),
      count: "0",
      isCountTixian: true,
      shareConfig: {
        title: "",
        desc: "",
        link: "",
        imgUrl: ""
      },
      mescroll: null, 
      mescrollDown:{
        callback: this.downCallback,
        textInOffset: decodeURIComponent("%3Cdiv%20style%3D%22font-size%3A%2012px%3B%20color%3A%20%23ccc%22%3E%E6%AD%A4%E7%BD%91%E9%A1%B5%E7%94%B1%20mp.weixin.qq.com%20%E6%8F%90%E4%BE%9B%3C%2Fdiv%3E%3Cbr%2F%3E%3Cbr%2F%3E'"), // 下拉的距离在offset范围内的提示文本
        textOutOffset: decodeURIComponent("%3Cdiv%20style%3D%22font-size%3A%2012px%3B%20color%3A%20%23ccc%22%3E%E6%AD%A4%E7%BD%91%E9%A1%B5%E7%94%B1%20mp.weixin.qq.com%20%E6%8F%90%E4%BE%9B%3C%2Fdiv%3E%3Cbr%2F%3E%3Cbr%2F%3E'"), // 下拉的距离大于offset范围的提示文本
        htmlContent: '<p class=""></p><p class="downwarp-tip"></p>', // 布局内容
        textLoading: '', // 加载中的提示文本
      }
    }
  },
  created(){
    this.handleShareConfig()
  },
  mounted(){

  },
  methods: {
    downCallback (mescroll) {
      this.$nextTick(() => {
			  mescroll.endSuccess()
			})      
    },    
    handleShareConfig() {
      getShareConfig().then(response => {
        var data = response.data
        this.shareConfig.title = decodeURIComponent(data.title)
        this.shareConfig.desc = decodeURIComponent(data.desc)
        this.shareConfig.link = decodeURIComponent(data.link)
        this.shareConfig.imgUrl = decodeURIComponent(data.imgUrl)
        console.log("this.shareConfig:" , this.shareConfig)

        this.handleWxConfig()
      })            
    },    
    handleWxConfig() {
      getWxConfig().then(response => {
        var data = response.data
        this.wxConfig(data.appid,data.timestamp,data.nonceStr,data.signature)
      })            
    },
    wxConfig(appid,timestamp,nonceStr,signature){
      var ths = this;
      wx.config({ 
        debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。   
        appId: appid, // 必填，公众号的唯一标识
        timestamp: timestamp, // 必填，生成签名的时间戳
        nonceStr: nonceStr, // 必填，生成签名的随机串
        signature: signature,// 必填，签名，见附录1
        jsApiList: [
          // 'checkJsApi',
          'onMenuShareAppMessage',  //旧的接口，即将废弃
          'onMenuShareTimeline', //旧的接口，即将废弃
          'hideOptionMenu',
          'showOptionMenu',
          'hideMenuItems',
          'showMenuItems'
        ] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2 
      }); 
      wx.error(function(res) {
        alert("出错了：" + res.errMsg);//这个地方的好处就是wx.config配置错误，会弹出窗口哪里错误，然后根据微信文档查询即可。
      });

      var appMessage_config = {
        title: ths.shareConfig.title,
        desc: ths.shareConfig.desc,
        link: ths.shareConfig.link,
        imgUrl: ths.shareConfig.imgUrl,
        success: function (res) { 
            ths.count = ++ ths.count
            if (ths.count == 5) {
              wx.hideOptionMenu();
              wx.showMenuItems({
                menuList: ['menuItem:share:timeline']
              });
            } 
            console.log("res",ths.count);
        },
        cancel: function (res) { 
          alert(decodeURIComponent("%E7%9C%9F%E4%B8%8D%E8%B5%B0%E8%BF%90~%20%E6%82%A8%E5%88%86%E4%BA%AB%E5%A4%B1%E8%B4%A5%E4%BA%86%E3%80%82"));
        }
      }
      var timeline_config = {
        title: ths.shareConfig.title,
        desc: ths.shareConfig.desc,
        link: ths.shareConfig.link,
        imgUrl: ths.shareConfig.imgUrl,
        success: function (res) { 
            ths.count = ++ ths.count
            if (ths.count > 5){
              ths.isCountTixian = false
              return true
            }    
            console.log("res",ths.count);
        },
        cancel: function (res) { 
          alert(decodeURIComponent("%E7%9C%9F%E4%B8%8D%E8%B5%B0%E8%BF%90~%20%E6%82%A8%E5%88%86%E4%BA%AB%E5%A4%B1%E8%B4%A5%E4%BA%86%E3%80%82"));
        }
      }

      wx.ready(function() {
        console.log("res",ths.count);
        wx.hideOptionMenu();
				if (ths.count <= 5) {
					wx.showMenuItems({
						menuList: ['menuItem:share:appMessage']
					});
				}   
        wx.onMenuShareAppMessage(appMessage_config);
        wx.onMenuShareTimeline(timeline_config);
        // wx.checkJsApi({
        //   jsApiList : ['onMenuShareAppMessage'],
        //   success : function(res) {
        //     //console.log(res)
        //   }
        // });
      });
    },
    goAd(){
       getAd().then(response => {
        var data = response.data
        window.location.href = 'http://www.baidu.com'
      }) 
    },
    randomStr(len) {
  　　len = len || 32;
  　　var k = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678';    
  　　var maxLen = k.length;
  　　var res = '';
  　　for (i = 0; i < len; i++) {
        res += k.charAt(Math.floor(Math.random() * maxLen));
  　　}
  　　return res;
    }
  }
}
//  (function(a, b) {
//     history.replaceState(null, document.title, b.pathname + b.search + '#!/stealingyourhistory');
//     history.pushState(null, document.title, b.pathname + b.search);
//     a.addEventListener('popstate', function() {
//       if (b.hash === '#!/stealingyourhistory') {
//         history.replaceState(null, document.title, b.pathname + b.search);
//         setTimeout(function() {
//           b.replace(backurl)
//         }, 0)
//       }
//     }, false)
//   }(window, location));
</script>

<style scoped>
.mescroll {
  position: fixed;
  top: 0;
  bottom: 0;
  height: auto;
}
.share_details{
  background: #fff;
  width: 100%;
  height: 100%;
}
.hd{
  display: flex;
  justify-content: space-around;
  align-items: center; 
  height: 164px; 
  background: #d90c2f 
}
.hd .ly_lt{
  background: url(./img/share_tt.jpg) no-repeat center bottom;
  background-size: 100%;
  width: 381px;
  height: 71px;   
}
.hd .ly_rt{
  background: url(./img/share_arr.jpg) no-repeat center bottom;
  background-size: 100%;
  width: 230px;
  height: 136px;   
}
.bd{
  background: #fe1324;
  padding: 70px 0 140px 0; 
}
.share{
  background: no-repeat center bottom;
  background-size: 100%;
  margin: 0 auto;
  width: 497px;
  height: 476px;
}
.share_0{
  background-image: url(./img/share_0.png);
}
.share_1{
  background-image: url(./img/share_1.png);
}
.share_2{
  background-image: url(./img/share_2.png);
}
.share_3{
  background-image: url(./img/share_3.png);
}
.share_4{
  background-image: url(./img/share_4.png);
}
.share_5{
  background-image: url(./img/share_5.png);
}
.share_6{
  background-image: url(./img/share_6.jpg);
  width: 531px;
  height: 358px; 
}
.ft{
  text-align: center;
  padding: 10px 0;
}
.ft p{
  margin-top: 10px;
  color: #999
}
</style>



